import React, { useState, useEffect } from "react";
import { MoveHr } from "../styled/StyledComponents";
import { sizes } from "../styled/Sizes";

// mblock
export const Mhr = (props) => {
  const [blockMove, setBlockMove] = useState(false);
  useEffect(() => {
    window.addEventListener("scroll", () => {
      let scroll;
      if (window.innerWidth < sizes.md) scroll = props.scrollHeightSm;
      else scroll = props.scrollHeightLg;
      if (document.documentElement.scrollTop > scroll) setBlockMove(true);
    });
  }, [props.scrollHeightSm, props.scrollHeightLg]);
  return (
    <MoveHr
      color={props.color}
      dir={props.dir}
      stretch={props.stretch}
      move={blockMove}
      flash={props.flash}
      smTop={props.smTop}
      lgTop={props.lgTop}
      width={props.width}
    ></MoveHr>
  );
};
