import React, { useState, useEffect } from "react";
import { MoveBlock, MoveBlockText } from "../styled/StyledComponents";
import { sizes } from "../styled/Sizes";

// mblock
export const MBlock = (props) => {
  const [blockMove, setBlockMove] = useState(false);
  const [visible, setVisible] = useState(false);
  useEffect(() => {
    window.addEventListener("scroll", () => {
      let scroll;
      if (window.innerWidth < sizes.md) scroll = props.scrollHeightSm;
      else scroll = props.scrollHeightLg;
      if (document.documentElement.scrollTop > scroll) {
        setBlockMove(true);
        if (!props.flash) {
          setInterval(() => {
            setVisible(true);
          }, 750);
        } else {
          setVisible(true);
        }
      }
    });
  }, [props.scrollHeightSm, props.scrollHeightLg, props.flash]);
  const content = () => {
    return (
      <div>
        <MoveBlockText visible={visible}>{props.text}</MoveBlockText>
      </div>
    );
  };
  return (
    <MoveBlock
      dir={props.dir}
      stretch={props.stretch}
      move={blockMove}
      flash={props.flash}
      top={props.top}
      height={props.height}
    >
      {content()}
    </MoveBlock>
  );
};
