import React from "react";
import {
  Parallax,
  GlobalStyle,
  TextBlock,
  LandingContainer,
  Footer,
} from "../styled/StyledComponents";
import { Header } from "../components/Header";
import { MBlock } from "../components/MoveBlock";
import { Mhr } from "../components/MoveHr";
import { Carousel } from "../components/Carousel";
import { Form } from "../components/Newsletter";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import facebook from "../assets/facebook.png";

// landing
const Landing = () => {
  return (
    <LandingContainer fluid>
      <Row noGutters>
        <GlobalStyle />
        <Col md={12}>
          <Header />
        </Col>
        <Col md={12}>
          <Parallax url="https://res.cloudinary.com/dyexps6g2/image/upload/v1610436095/assorted_znarsh.jpg"></Parallax>
          <MBlock
            text="Taste. Class. Perfection"
            dir="right"
            stretch={true}
            flash={false}
            top="20em"
            height="200px"
            scrollHeightSm={20}
            scrollHeightLg={20}
          />
        </Col>
        <Mhr
          color="wheat"
          dir="left"
          stretch={true}
          flash={false}
          smTop="430px"
          lgTop="430px"
          width="8px"
          scrollHeightSm={20}
          scrollHeightLg={20}
        />
        <Col lg={6}>
          <Carousel />
        </Col>
        <TextBlock id="About" lg={6}>
          <div>
            CoffeeElite has specialized in selecting and roasting some of the
            world's most exclusive coffees. Named the world's best coffee shop
            by The Daily Telegraph for two years in a row, we strive to have you
            Taste the Joy of the extraordinary.
          </div>
        </TextBlock>
        <Col md={12} id="Eat">
          <Parallax
            url="https://res.cloudinary.com/dyexps6g2/image/upload/v1610436095/assorted_znarsh.jpg"
            height="1100px"
          ></Parallax>
          <MBlock
            text="100% Fairtrade Arabica Coffee"
            dir="left"
            stretch={false}
            flash={false}
            top="50px"
            height="300px"
            scrollHeightSm={1000}
            scrollHeightLg={900}
          />
          <Mhr
            color="wheat"
            dir="right"
            stretch={true}
            flash={false}
            smTop="280px"
            lgTop="146px"
            width="40px"
            scrollHeightSm={1000}
            scrollHeightLg={900}
          />
          <MBlock
            text="Breakfast, Lunch & Dinner"
            dir="right"
            stretch={false}
            flash={false}
            top="400px"
            height="300px"
            scrollHeightSm={1300}
            scrollHeightLg={1200}
          />
          <Mhr
            color="wheat"
            dir="left"
            stretch={true}
            flash={false}
            smTop="630px"
            lgTop="490px"
            width="40px"
            scrollHeightSm={1300}
            scrollHeightLg={1200}
          />
          <MBlock
            text="Delictable Sweet Treats"
            dir="left"
            stretch={false}
            flash={false}
            top="750px"
            height="300px"
            scrollHeightSm={1600}
            scrollHeightLg={1500}
          />
          <Mhr
            color="wheat"
            dir="right"
            stretch={true}
            flash={false}
            smTop="980px"
            lgTop="850px"
            width="40px"
            scrollHeightSm={1600}
            scrollHeightLg={1500}
          />
        </Col>
        <Col md={12}>
          <Parallax url="https://res.cloudinary.com/dyexps6g2/image/upload/v1609769602/coffee_shop_tmhhc8.jpg"></Parallax>
        </Col>
        <Col md={12} id="Reviews">
          <Parallax
            url="https://res.cloudinary.com/dyexps6g2/image/upload/v1610436095/assorted_znarsh.jpg"
            height="900px"
          ></Parallax>
          <MBlock
            text='"The best coffee I have ever had!" - Kate, Australia'
            dir="right"
            stretch={true}
            flash={false}
            top="0"
            height="300px"
            scrollHeightSm={2700}
            scrollHeightLg={2600}
          />
          <MBlock
            text='"Delicous cake and treats" - Adam, USA'
            dir="left"
            stretch={true}
            flash={false}
            top="300px"
            height="300px"
            scrollHeightSm={3000}
            scrollHeightLg={2900}
          />
          <MBlock
            text='"Cannot wait to return!" - Mary, London'
            dir="right"
            stretch={true}
            flash={false}
            top="600px"
            height="300px"
            scrollHeightSm={3300}
            scrollHeightLg={3200}
          />
        </Col>
        <Col md={12}>
          <Parallax url="https://res.cloudinary.com/dyexps6g2/image/upload/v1610453286/pour_qmwwky.jpg"></Parallax>
        </Col>
        <Col md={12} id="Contact">
          <Parallax
            url="https://res.cloudinary.com/dyexps6g2/image/upload/v1610436095/assorted_znarsh.jpg"
            height="1100px"
          ></Parallax>
          <MBlock
            text="Email: coffeeElite@gmail"
            dir="left"
            stretch={true}
            flash={true}
            top="50px"
            height="300px"
            scrollHeightSm={4300}
            scrollHeightLg={4200}
          />
          <MBlock
            text="Address: 12 Harrington Sreet, Cape Town"
            dir="right"
            stretch={true}
            flash={true}
            top="400px"
            height="300px"
            scrollHeightSm={4600}
            scrollHeightLg={4500}
          />
          <MBlock
            text="Opening Times: 8am - 5pm, Monday - Sunday"
            dir="left"
            stretch={true}
            flash={true}
            top="750px"
            height="300px"
            scrollHeightSm={4900}
            scrollHeightLg={4800}
          />
        </Col>
        <Col md={12}>
          <Parallax url="https://res.cloudinary.com/dyexps6g2/image/upload/v1609770249/coffee_barista_sc7zjj.jpg"></Parallax>
        </Col>
        <Footer lg={12}>
          <div>coffeeElite@gmail.com</div>
          <img src={facebook} alt="facebook logo" />
          <div>©Harison Lacey</div>
          <Mhr
            color="wheat"
            dir="left"
            stretch={true}
            flash={false}
            smTop="0"
            lgTop="0"
            width="8px"
            scrollHeightSm={6000}
            scrollHeightLg={5800}
          />
          <Mhr
            color="wheat"
            dir="right"
            stretch={true}
            flash={false}
            smTop="260px"
            lgTop="260px"
            width="8px"
            scrollHeightSm={6000}
            scrollHeightLg={5800}
          />
        </Footer>
        <Form />
      </Row>
    </LandingContainer>
  );
};

export default Landing;
